/* The navigation menu */
.topbar {
    overflow: hidden;
    background-color: #333;
}
  
  /* Navigation links */
.topbar a {
    float: left;
    font-size: 16px;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}
  
  /* The subnavigation menu */
.subnav {
    float: left;
    overflow: hidden;
}
  
  /* Subnav button */
.subnav .subnavbtn {
    font-size: 16px;
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
}
  
  /* Add a red background color to navigation links on hover */
.topbar a:hover, .subnav:hover .subnavbtn {
    background-color:#2e4ead;
}
  
  /* Style the subnav content - positioned absolute */
.subnav-content {
    display: none;
    position: absolute;
    left:auto;
    right:auto;
    background-color:#2e4ead;
    width: 50%;
    z-index: 1;
}
  
  /* Style the subnav links */
.subnav-content a {
    float: left ;
    color: white;
    text-decoration: none;
}
  
  /* Add a grey background color on hover */
.subnav-content a:hover {
    background-color: #92a6e2;
    color: black;
}
  
  /* When you move the mouse over the subnav container, open the subnav content */
.subnav:hover .subnav-content {
    display: block;
}
